import { z } from 'zod';
import {
  AnyMessageContent,
} from "@whiskeysockets/baileys";

// Zod schemas for all interfaces except the three small ones at the bottom

export const OwnerSchema = z.object({
  lid: z.string(),
  jid: z.string(),
  name: z.string().optional()
}).strict();

export type Owner = z.infer<typeof OwnerSchema>;

export const BotConfigSchema = z.object({
  phoneNumber: z.string().regex(/^(\+?\d+|0\d+)$/, 'Invalid phone number format'),
  namebot: z.string().optional(),
  fromMe: z.boolean().nullable().default(null),
  sessionPath: z.string().default('./session'),
  autoReconnect: z.boolean().default(true),
  reconnectDelay: z.number().min(1000).default(3000),
  maxReconnectAttempts: z.number().min(1).default(10),
  showLogs: z.boolean().default(false),
  printQR: z.boolean().default(false),
  markOnline: z.boolean().default(false),
  browser: z.string().default(['Chrome', 'Firefox', 'Safari', 'Edge'][Math.floor(Math.random() * 4)]),
  syncHistory: z.boolean().default(false),
  autoRead: z.boolean().default(false),
  linkPreview: z.boolean().default(true),
  owners: z.array(OwnerSchema).default([]),
  commandsPath: z.string().default('./plugins'),
  prefix: z.union([z.string(), z.array(z.string())]).default('!'),
  onConnected: z.function().optional(),
  onDisconnected: z.function().optional(),
  onError: z.custom<(error: Error) => void>()
  .optional(),
}).strict();

export type BotConfig = z.infer<typeof BotConfigSchema>;

// Using z.custom for complex function types
export const MessageSchema = z.object({
  id: z.string().optional(),
  body: z.string(),
  text: z.string(),
  from: z.string(),
  name: z.string().optional(),
  pushName: z.string().optional(),
  timestamp: z.date(),
  isGroup: z.boolean(),
  isOwner: z.boolean(),
  isAdmin: z.boolean(),
  isBotAdmin: z.boolean(),
  fromMe: z.boolean(),
  key: z.any().optional(),
  message: z.any().optional(),
  raw: z.any(),
  args: z.array(z.string()).optional(),
  command: z.string().optional(),
  prefix: z.string().optional(),
  sender: z.string(),
  chat: z.string(),
  reply: z.custom<(text: string, options?: Partial<AnyMessageContent>) => Promise<any>>(),
  react: z.custom<(emoji: string) => Promise<any>>(),
  lid2jid: z.custom<(i: string, id: string) => Promise<any>>().optional(),
  jid2lid: z.custom<(i: string, id: string) => Promise<any>>().optional(),
  delete: z.custom<() => Promise<any>>(),
  download: z.custom<() => Promise<Buffer | null>>(),
  forward: z.custom<(jid: string, options?: Partial<AnyMessageContent>) => Promise<any>>(),
  typing: z.custom<(duration?: number) => Promise<void>>(),
  recording: z.custom<(duration?: number) => Promise<void>>()
}).strict();

export type Message = z.infer<typeof MessageSchema>;

export const GroupEventSchema = z.object({
  id: z.string(),
  chat: z.string(),
  userUrl: z.string().optional(),
  participants: z.array(z.string()),
  action: z.enum(['add', 'remove', 'promote', 'demote']),
  author: z.string().optional(),
  authorUrl: z.string().optional(),
  timestamp: z.date()
}).strict();

export type GroupEvent = z.infer<typeof GroupEventSchema>;

// New schemas for CommandOptions and CommandContext
export const CommandContextSchema = z.object({
  conn: z.any(),
  text: z.string(),
  args: z.array(z.string()),
  command: z.string(),
  prefix: z.string(),
  bot: z.any()
}).strict();

export type CommandContext = z.infer<typeof CommandContextSchema>;

// Custom type for Handler function
type Handler = (message: Message, context: CommandContext, bot: any) => Promise<any>;

export const CommandOptionsSchema = z.object({
  name: z.string().optional(),
  command: z.union([z.string(), z.array(z.string())]).optional(),
  aliases: z.array(z.string()).optional(),
  description: z.string().optional(),
  category: z.string().optional(),
  usage: z.string().optional(),
  cooldown: z.number().optional(),
  ownerOnly: z.boolean().optional(),
  groupOnly: z.boolean().optional(),
  privateOnly: z.boolean().optional(),
  botAdmin: z.boolean().optional(),
  disabled: z.boolean().optional(),
  usePrefix: z.boolean().optional(),
  before: z.custom<Handler>().optional(),
  after: z.custom<Handler>().optional(),
  execute: z.custom<(message: Message, context: CommandContext, bot: any) => Promise<any>>().optional()
}).strict().catchall(z.any()); // catchall for [key: string]: any

export type CommandOptions = z.infer<typeof CommandOptionsSchema>;

// The three small ones at the bottom - kept as interfaces/types
export interface MessageHandler {
  (msg: Message): Promise<string | null | void | any>;
}

export interface AccessHandler {
  (msg: Message, checkType: string, ...args: any[]): any;
}

export interface GroupEventHandler {
  (ctx: any, event: GroupEvent, eventType: string): any;
}