type Colors = 
    | 'red' | 'green' | 'yellow' | 'blue' | 'gray'
    | 'magenta' | 'cyan' | 'white' | 'bgRed' | 'bgGreen' 
    | 'bgYellow' | 'bgBlue' | 'bgMagenta' | 'bgCyan';

/* object was the colors */

const colors: Record<Colors, string> = {
    red: "\x1b[31m",
    green: "\x1b[32m",
    yellow: "\x1b[33m",
    blue: "\x1b[34m",
    gray: "\x1b[90m",
    magenta: "\x1b[35m",
    cyan: "\x1b[36m",
    white: "\x1b[37m",
    bgRed: "\x1b[41m",
    bgGreen: "\x1b[42m",
    bgYellow: "\x1b[43m",
    bgBlue: "\x1b[44m",
    bgMagenta: "\x1b[45m",
    bgCyan: "\x1b[46m"
};

/* not library */
const color = (text: string, color: Colors) => `${colors[color] + text}\x1b[0m`;

/* rainbow function  */
const rainbow = (text: string): string => {
    const rainbowColors: Colors[] = ['red', 'yellow', 'green', 'cyan', 'blue', 'magenta'];
    return text.split('').map((char, index) => {
        const colorIndex = index % rainbowColors.length;
        return `${colors[rainbowColors[colorIndex]] + char}\x1b[0m`;
    }).join('');
};

export { color, rainbow };