import { DisconnectReason } from "@whiskeysockets/baileys";
import qrTerminal from "qrcode-terminal";
import UI from "../uilts/logger.js";
import { color } from "../uilts/colors.js";

const connection_event = async (OOP: any, isNewSession: boolean) => {
  try {
   const { sock } = OOP;
    sock.ev.on('connection.update', (update: any) => {
      const { connection, lastDisconnect, qr } = update;
      const { config, reconnectTimeout, reconnectAttempts } = OOP;
      const { showLogs, printQR, autoReconnect, maxReconnectAttempts, reconnectDelay, onConnected, onDisconnected, namebot } = config;

      if (qr && printQR && showLogs) {
        UI.line();
        UI.done('Scan QR code to login ');
        qrTerminal.generate(qr, { small: true });
        UI.line();
      }

      if (connection === 'close') {
        if (reconnectTimeout) {
          clearTimeout(reconnectTimeout);
        }
        
        if (lastDisconnect?.error?.output?.statusCode === DisconnectReason.loggedOut) {
          if (showLogs) {
            UI.error(' Session logged out. Please re-pair the device.');
          }
          OOP.isRunning = false;
          if (onDisconnected) {
            onDisconnected();
          }
          return;
        }

        if (autoReconnect) {
          if (reconnectAttempts < maxReconnectAttempts) {
            OOP.reconnectAttempts++;
            if (showLogs) {
              UI.loading(`Reconnecting... (${OOP.reconnectAttempts} ~ ${maxReconnectAttempts})`);
            }
            OOP.reconnectTimeout = setTimeout(() => OOP.restart(), reconnectDelay);
          } else {
            OOP.isRunning = false;
            if (onDisconnected) {
              onDisconnected();
            }
          }
        } else {
          OOP.isRunning = false;
          if (onDisconnected) {
            onDisconnected();
          }
        }
      }

      if (connection === 'open') {
      (async () => {
        await sock. uploadPreKeysToServerIfRequired()
        await sock.KeysMessageWA()
       })()
        OOP.reconnectAttempts = 0;
        if (reconnectTimeout) {
          clearTimeout(reconnectTimeout);
        }
        if (onConnected) {
          onConnected();
        }
        if (showLogs) {
          UI.clear();
          UI.random(`${namebot || "WhatsApp Bot"} started successfully`);
          console.log(`      ______ ______
    _/      Y      \_
   // ~~ ~~ | ~~ ~  \\
  // ~ ~ ~~ | ~~~ ~~ \\      Original ${namebot || "WhatsApp Bot"}
 //________.|.________\\     Created by: ${color("@veni_xov", "yellow")}
\`----------\`-'----------'`)
          UI.line()
          UI.info('Bot Engine    : Vii7/whatsapp')
          UI.info('Backend       : Node.js & Type Script')
          UI.line()
        }
      }
    });
  } catch (error) {
    if (OOP.config.onError) {
      if (typeof OOP.config.onError === 'function') {
        OOP.config.onError(error);
      }
    }
  }
};

export default connection_event;