import UI from "./logger.js"
import { loadCommandsFromPath } from "../enhancers/index.js";
import { color } from "./colors.js";

const loadCommands = async (OOP: any) => {
    try {
      await loadCommandsFromPath(OOP.config.commandsPath, OOP.commandSystem);
      
      if (OOP.config.showLogs) {
      console.log(color(`       _,    _   _    ,_
  .o888P     Y8o8Y     Y888o.
 d88888      88888      88888b
d888888b_  _d88888b_  _d888888b
8888888888888888888888888888888
8888888888888888888888888888888
YJGS8P"Y888P"Y888P"Y888P"Y8888P
 Y888   '8'   Y8P   '8'   888Y
  '8o          V          o8'
    \`                     \``, "red"))
      UI.line();
      UI.info(`Loaded (${OOP.commandSystem.getAll().length}) commands in "${OOP.config.commandsPath}"`);
      UI.info(`Name Bot: ${OOP.namebot || "WhatsApp Bot"}`)
      UI.line()
      };
    } catch (error: any) {
      UI.error(`[${new Date().toLocaleTimeString()}] Error loading Commands: ${error.message}`);
    }
}

export default loadCommands;