import { readdir } from 'fs/promises';
import { pathToFileURL, fileURLToPath } from 'url';
import { createRequire } from 'module';
import { resolve, join, dirname, relative } from 'path';
import { Message, CommandOptions, CommandContext, BotConfig } from "../types/types.js";
import { extractPrefix } from './prefix.js';
import { watch, FSWatcher } from 'chokidar';
import { existsSync } from 'fs';
import { cmdControl } from "../control/index.js";
import UI from "../uilts/logger.js";

type Handler = (msg: Message, ctx: any, bot?: any) => Promise<boolean | void>;
type Middleware = (msg: Message, next: () => Promise<void>) => Promise<void>;

interface CooldownCache {
  [key: string]: number;
}

declare global {
  var cooldownCache: Map<string, number>;
  var botInstance: any;
}

const __filename = fileURLToPath(import.meta.url);
const __dirname = dirname(__filename);

const COMMAND_PROPS = {
  basic: ['command', 'name', 'description', 'aliases', 'category', 'usage'],
  restrictions: ['owner', 'group', 'private', 'botAdmin', 'admin'],
  settings: ['cooldown', 'disabled', 'usePrefix'],
  hooks: ['before', 'after']
};

const ALL_PROPS = [
  ...COMMAND_PROPS.basic,
  ...COMMAND_PROPS.restrictions,
  ...COMMAND_PROPS.settings,
  ...COMMAND_PROPS.hooks
];

export class CommandSystem {
  private bot: any;
  private commands: CommandOptions[] = [];
  private middlewares: Middleware[] = [];
  private beforeHandlers: Handler[] = [];
  private afterHandlers: Handler[] = [];
  private categories: Set<string> = new Set();
  private watcher: FSWatcher | null = null;
  private fileCommandMap: Map<string, string[]> = new Map();
  private watchedPath: string = '';

  constructor(bot?: any) {
    this.bot = bot;
  }

  setBot(bot: any): void {
    this.bot = bot;
  }

  register(command: CommandOptions | Function): void {
    let cmdObj: CommandOptions;

    if (typeof command === 'function') {
      const fn = command as any;
      cmdObj = { 
        execute: async (msg: Message, ctx: CommandContext, bot: any) => fn(msg, ctx, bot)
      };
      
      for (const p of ALL_PROPS) { 
        if (fn[p] !== undefined) (cmdObj as any)[p] = fn[p];
      }
      
      for (const key in fn) {
        if (!ALL_PROPS.includes(key) && key !== 'execute') {
          (cmdObj as any)[key] = fn[key];
        }
      }
    } else {
      cmdObj = { ...command };
    }

    const names = this.getNames(cmdObj);
    if (names.length === 0) return;

    this.commands.push(cmdObj);
    if (cmdObj.category) this.categories.add(cmdObj.category);
    
    const filePath = (cmdObj as any).__filePath;
    if (filePath) {
      const normalizedPath = resolve(filePath);
      this.fileCommandMap.set(normalizedPath, names);
    }
  }

  registerBeforeHandler(handler: Handler): void {
    this.beforeHandlers.push(handler);
  }

  unregister(commandName: string): boolean {
    const before = this.commands.length;
    this.commands = this.commands.filter(cmd => !this.getNames(cmd).includes(commandName.toLowerCase()));
    return this.commands.length < before;
  }

  private getNames(cmd: CommandOptions): string[] {
    const names: string[] = [];
    if (cmd.command) {
      const cmdNames = Array.isArray(cmd.command) ? cmd.command : [cmd.command];
      names.push(...cmdNames);
    }
    if (cmd.name) names.push(cmd.name);
    if (cmd.aliases) names.push(...cmd.aliases);
    return names.map(n => n.toLowerCase());
  }

  findCommand(name: string): CommandOptions | null {
    return this.commands.find(cmd => this.getNames(cmd).includes(name.toLowerCase())) ?? null;
  }

  getCommandsByCategory(category?: string): CommandOptions[] {
    return category ? this.commands.filter(cmd => cmd.category === category) : [...this.commands];
  }

  getAllCategories(): string[] {
    return Array.from(this.categories);
  }

  getAll(): CommandOptions[] {
    return [...this.commands];
  }

  use(middleware: Middleware): void {
    this.middlewares.push(middleware);
  }

  before(handler: Handler): void {
    this.beforeHandlers.push(handler);
  }

  after(handler: Handler): void {
    this.afterHandlers.push(handler);
  }

  async processMessage(msg: Message, ctx: any, config: any): Promise<boolean> {
    if (!msg.body) return false;

    const context: CommandContext = {
      conn: ctx.sock,
      text: msg.body,
      args: [],
      command: '',
      prefix: '',
      bot: ctx
    };

    for (const handler of this.beforeHandlers) {
      try {
        let result;
        if (handler.length === 2) {
          result = await handler(msg, context);
        } else {
          result = await (handler as any)(msg, context, this.bot);
        }
        if (result === true) return true;
      } catch (error: any) { 
        UI.error(`[${new Date().toISOString()}] Error in before handler: ${error.message}`);
      }
    }

    for (const cmd of this.commands) {
      if (cmd.before) {
        try {
          const result = await (cmd.before as any)(msg, context, this.bot);
          if (result === true) return true;
        } catch (error: any) {
          UI.error(`[${new Date().toISOString()}] Error in command before ${error.message}`);
        }
      }
    }
    
    const text = msg.body.trim();
    const prefixes = config || ["."];
    const extracted = extractPrefix(text, prefixes);

    if (extracted) {
      const cmd = this.findCommand(extracted.command);
      if (!cmd?.execute) return false;

      const cmdContext: CommandContext = {
        conn: ctx.sock,
        text: extracted.args.join(' '),
        args: extracted.args,
        command: extracted.command,
        prefix: extracted.prefix,
        bot: ctx
      };

      let executed = false;
      await this.runMiddlewares(msg, async () => {
        await this.executeCommand(cmd!, msg, cmdContext);
        executed = true;
      });

      if (!executed) return false;

      if (cmd.after) {
        try { await cmd.after(msg, cmdContext, this.bot); } 
        catch (error) { /*  */ }
      }

      for (const handler of this.afterHandlers) {
        try { await handler(msg, { command: extracted.command, args: extracted.args, context: cmdContext }); } 
        catch (error) { /*  */ }
      }

      return true;
    }
    
    for (const cmd of this.commands) {
      if (cmd.usePrefix === false && cmd.execute) {
        const args = text.split(/\s+/);
        const cmdName = args.shift()?.toLowerCase() || '';
        if (this.getNames(cmd).includes(cmdName)) {
          const cmdContext: CommandContext = {
            conn: ctx.sock,
            text: args.join(' '),
            args,
            command: cmdName,
            prefix: '',
            bot: ctx
          };
          await this.executeCommand(cmd, msg, cmdContext);
          return true;
        }
      }
    }
    
    return false;
  }

  private async executeCommand(cmd: CommandOptions, msg: Message, context: CommandContext): Promise<void> {
    const checks: Array<[boolean | undefined, string, any?]> = [
      [cmd.disabled, 'disabled'],
      [cmd.owner && !msg.isOwner, 'owner'],
      [cmd.group && !msg.isGroup, 'group'],
      [cmd.admin && !msg.isAdmin, 'admin'],
      [cmd.private && msg.isGroup, 'private'],
      [cmd.botAdmin && !msg.isBotAdmin, 'botAdmin'],
    ];

    for (const [failed, type, extra] of checks) {
      if (failed) { 
        await cmdControl(this.bot, msg, type, extra);
        return; 
      }
    }

    if (cmd.cooldown) {
      if (!global.cooldownCache) global.cooldownCache = new Map();
      
      const key = `${msg.sender}:${this.getNames(cmd)[0]}`;
      const now = Date.now();
      const lastUsed = global.cooldownCache.get(key);
      
      if (lastUsed) {
        const timePassed = (now - lastUsed) / 1000;
        const remaining = Math.ceil(cmd.cooldown - timePassed);
        
        if (remaining > 0) { 
          await cmdControl(this.bot, msg, 'cooldown', remaining);
          return; 
        }
      }
    }

    msg.args = context.args;
    msg.prefix = context.prefix;
    msg.command = Array.isArray(cmd.command) ? cmd.command[0] : cmd.command || 'unknown';

    try {
      await cmd.execute!(msg, context, this.bot);
      
      if (cmd.cooldown) {
        const key = `${msg.sender}:${this.getNames(cmd)[0]}`;
        global.cooldownCache.set(key, Date.now());
      }
    } catch (error: any) {
      UI.error(`Error executing command ${msg.command}: ${error.message}`);
      await cmdControl(this.bot, msg, 'error', error);
    }
  }

  private async runMiddlewares(msg: Message, final: () => Promise<void>): Promise<void> {
    let i = 0;
    const next = async () => {
      i < this.middlewares.length ? await this.middlewares[i++](msg, next) : await final();
    };
    await next();
  }

  clearCooldowns(): void {
    if (global.cooldownCache) global.cooldownCache.clear();
  }

  getStats(): { total: number; categories: number; middlewares: number } {
    return {
      total: this.commands.length,
      categories: this.categories.size,
      middlewares: this.middlewares.length
    };
  }

  async loadFile(filePath: string): Promise<void> {
    try {
      const normalizedPath = resolve(filePath);
      const commands = await this.importFile(normalizedPath);
      
      if (commands) {
        const cmdArray = Array.isArray(commands) ? commands : [commands];
        
        for (const cmd of cmdArray) {
          if (typeof cmd === 'function') {
            (cmd as any).__filePath = normalizedPath;
            this.registerBeforeHandler(cmd);
            const fileName = normalizedPath.split(/[\/\\]/).pop();
            // UI.info(`[${new Date().toISOString()}] before handler (${fileName})`);
          } else if (cmd && (cmd as CommandOptions).execute) {
            (cmd as any).__filePath = normalizedPath;
            this.register(cmd as CommandOptions);
          }
        }
      }
    } catch (error: any) {
      UI.error(`[${new Date().toISOString()}] ${filePath}: ${error.message}`);
    }
  }

  async importFile(filePath: string): Promise<CommandOptions | CommandOptions[] | Handler | null> {
    const normalizedPath = resolve(filePath);
    const isESM = this.isESMFile(normalizedPath);
    
    try {
      if (isESM) {
        const fileUrl = pathToFileURL(normalizedPath).href;
        const cacheBuster = `reload=${Date.now()}&r=${Math.random().toString(36).substring(7)}`;
        const module = await import(`${fileUrl}?${cacheBuster}`);
        const exported = module.default ?? module;
        return this.normalizeExports(exported, normalizedPath);
      } else {
        const require = createRequire(import.meta.url);
        
        try {
          const resolvedPath = require.resolve(normalizedPath);
          delete require.cache[resolvedPath];
          const exported = require(resolvedPath);
          return this.normalizeExports(exported, normalizedPath);
        } catch {
          const fileUrl = pathToFileURL(normalizedPath).href;
          const module = await import(`${fileUrl}?t=${Date.now()}`);
          const exported = module.default ?? module;
          return this.normalizeExports(exported, normalizedPath);
        }
      }
    } catch (error: any) {
      UI.error(`[${new Date().toISOString()}] ${filePath}: ${error.message}`);
      return null;
    }
  }

  private isESMFile(filePath: string): boolean {
    const ext = filePath.split('.').pop()?.toLowerCase();
    
    if (ext === 'mjs') return true;
    if (ext === 'cjs') return false;
    
    if (ext === 'js' || ext === 'ts') {
      try {
        const fs = require('fs');
        const path = require('path');
        let dir = path.dirname(filePath);
        const root = path.parse(dir).root;
        
        while (dir !== root) {
          const pkgPath = path.join(dir, 'package.json');
          if (fs.existsSync(pkgPath)) {
            const pkg = JSON.parse(fs.readFileSync(pkgPath, 'utf8'));
            return pkg.type === 'module';
          }
          dir = path.dirname(dir);
        }
      } catch {}
    }
    
    return true;
  }

  normalizeExports(exported: any, filePath: string): CommandOptions | CommandOptions[] | Handler | null {
    if (!exported) return null;

    if (Array.isArray(exported)) {
      return exported.map(item => this.normalizeExports(item, filePath)).filter(Boolean) as CommandOptions[];
    }

    if (typeof exported === 'function') {
      const fnName = exported.name || '';
      const fnString = exported.toString();
      
      if (fnName === 'before' || fnString.includes('function before')) {
        return exported as Handler;
      }
      
      if (exported.execute || exported.command || exported.name || exported.aliases) {
        const cmd: CommandOptions = { execute: exported };
        
        for (const prop of ALL_PROPS) {
          if (exported[prop] !== undefined) {
            (cmd as any)[prop] = exported[prop];
          }
        }
        
        return cmd;
      }
      
      return exported as Handler;
    }

    if (typeof exported === 'object') {
      if (exported.execute && typeof exported.execute === 'function') {
        return { ...exported };
      } else if (exported.default) {
        return this.normalizeExports(exported.default, filePath);
      } else {
        const fnKeys = ['run', 'handler', 'handle', 'exec', 'main'];
        for (const key of fnKeys) {
          if (typeof exported[key] === 'function') {
            return { ...exported, execute: exported[key] };
          }
        }
      }
    }

    const cmd: CommandOptions = { ...exported };
    if (cmd.execute) {
      if (!cmd.command && !cmd.name) {
        const fileName = filePath.split(/[\/\\]/).pop()?.replace(/\.[^.]+$/, '');
        if (fileName) cmd.command = [fileName];
      }
      return cmd;
    }

    return null;
  }

  startWatching(commandsPath: string): void {
    if (this.watcher) {
      this.watcher.close();
      this.watcher = null;
    }

    const absolutePath = resolve(process.cwd(), commandsPath);
    this.watchedPath = absolutePath;

    if (!existsSync(absolutePath)) {
      UI.error(`Path does not exist: ${absolutePath}`);
      return;
    }

    this.watcher = watch(absolutePath, {
      ignored: [/(^|[\/\\])\../, /node_modules/, /\.git/],
      persistent: true,
      ignoreInitial: true,
      awaitWriteFinish: { stabilityThreshold: 300, pollInterval: 100 },
      depth: 99,
      usePolling: true,
      interval: 1000,
      binaryInterval: 1000,
      alwaysStat: true,
      atomic: true
    });

    this.watcher
      .on('ready', () => UI.done('Watcher ready'))
      .on('add', path => {
        if (!this.isCommandFile(path)) return;
        UI.info(`[${new Date().toISOString()}] New: ${this.getRelativePath(path)} `);
        this.loadFile(path).catch(err => UI.error(`[${new Date().toISOString()}] error ${err.message}`));
      })
      .on('change', path => {
        if (!this.isCommandFile(path)) return;
        UI.info(`[${new Date().toISOString()}] Changed: ${this.getRelativePath(path)} `);
        this.reloadFile(path).catch(err => UI.error(`[${new Date().toISOString()}] error ${err.message}`));
      })
      .on('unlink', path => {
        if (!this.isCommandFile(path)) return;
        console.log(`[${new Date().toISOString()}] Deleted: ${this.getRelativePath(path)} `);
        this.unloadFile(path);
      })
      .on('error', (error: any) => UI.error(` Watcher error: ${error.message}`));
  }
  
  private async reloadFile(filePath: string): Promise<void> {
    const normalizedPath = resolve(filePath);
    this.unloadFile(normalizedPath);
    await new Promise(resolve => setTimeout(resolve, 500));
    await this.loadFile(normalizedPath);
  }

  private unloadFile(filePath: string): void {
    const normalizedPath = resolve(filePath);
    const commandNames = this.fileCommandMap.get(normalizedPath);
    
    if (!commandNames?.length) {
      this.commands = this.commands.filter(cmd => {
        const cmdPath = (cmd as any).__filePath;
        return !cmdPath || resolve(cmdPath) !== normalizedPath;
      });
    } else {
      commandNames.forEach(name => this.unregister(name));
    }
    
    this.beforeHandlers = this.beforeHandlers.filter(handler => {
      const handlerPath = (handler as any).__filePath;
      return !handlerPath || resolve(handlerPath) !== normalizedPath;
    });
    
    this.fileCommandMap.delete(normalizedPath);
  }

  private isCommandFile(filePath: string): boolean {
    const ext = filePath.split('.').pop()?.toLowerCase();
    return ['js', 'ts', 'mjs', 'cjs'].includes(ext || '');
  }

  private getRelativePath(filePath: string): string {
    try {
      return relative(this.watchedPath, filePath);
    } catch {
      return filePath;
    }
  }

  stopWatcher(): void {
    if (this.watcher) {
      this.watcher.close();
      this.watcher = null;
      console.log(' Watcher stopped');
    }
  }
}

export async function loadCommandsFromPath(
  commandsPath: string, 
  commandSystem?: CommandSystem
): Promise<CommandOptions[]> {
  const absolutePath = resolve(process.cwd(), commandsPath);

  const scanDir = async (dir: string): Promise<void> => {
    try {
      const items = await readdir(dir, { withFileTypes: true });
      
      for (const item of items) {
        const fullPath = join(dir, item.name);
        
        if (item.isDirectory()) {
          await scanDir(fullPath);
        } else if (item.isFile()) {
          const ext = item.name.split('.').pop()?.toLowerCase();
          if (ext && ['js', 'ts', 'mjs', 'cjs'].includes(ext) && commandSystem) {
            await commandSystem.loadFile(fullPath);
          }
        }
      }
    } catch (error: any) {
      UI.error(`${dir}: ${error.message}`);
    }
  };

  await scanDir(absolutePath);
  if (commandSystem) {
    commandSystem.startWatching(commandsPath);
  }

  return commandSystem?.getAll() || [];
}

if (!global.cooldownCache) {
  global.cooldownCache = new Map();
}