import { GroupEvent } from "../types/types.js";

const GROUP_MESSAGES = {
    add: (p: { phoneNumber: string }[], author?: string) => {
        const names = p.map(x => `@${x.phoneNumber.split('@')[0]}`).join(", ");
        let msg = ` *Welcome* ${names}`;
        if (author) msg += `\n_Added by: @${author.split('@')[0]}_`;
        return msg;
    },
    remove: (p: { phoneNumber: string }[], author?: string) => {
        const names = p.map(x => `@${x.phoneNumber.split('@')[0]}`).join(", ");
        let msg = ` *Goodbye* ${names}`;
        if (author) msg += `\n_Removed by: @${author.split('@')[0]}_`;
        return msg;
    },
    promote: (p: { phoneNumber: string }[], author?: string) => {
        const names = p.map(x => `@${x.phoneNumber.split('@')[0]}`).join(", ");
        let msg = ` ${names} *promoted to admin*`;
        if (author) msg += `\n_By: @${author.split('@')[0]}_`;
        return msg;
    },
    demote: (p: { phoneNumber: string }[], author?: string) => {
        const names = p.map(x => `@${x.phoneNumber.split('@')[0]}`).join(", ");
        let msg = ` ${names} *demoted from admin*`;
        if (author) msg += `\n_By: @${author.split('@')[0]}_`;
        return msg;
    }
  }
export async function groupControl(
    bot: any,
    event: GroupEvent,
    eventType: string,
): Promise<void> {
    if (bot.userGroupEventHandler) {
        const result = await bot.userGroupEventHandler(bot, event, eventType);
        if (result !== undefined || result === null) {
            return; 
        }
    }
    console.log("cmd", typeof bot)
    console.log("cmd", typeof event)
    if (!bot.sock) return;

    const messageFunc: any = GROUP_MESSAGES[eventType as keyof typeof GROUP_MESSAGES];
    if (!messageFunc) return;

    const mentionedJids = [
        ...event.participants.map((p: any) => p.phoneNumber),
        ...(event.author ? [event.author] : []),
    ];

    const text = messageFunc(event.participants, event.author);

    await bot.sock.msgUrl(event.id, text, {
        img: "https://files.catbox.moe/hm9iq4.jpg",
        title: bot.config.namebot || "WhatsApp Bot",
        mentions: mentionedJids,
        big: false
     })
}